<template>
    <div>
        <!--        <div id="tocContainer" class="toc-container" style="z-index: 0"/>-->
        <div id="editor-md" style="z-index: 5">
            <textarea style="display:none;"/>
        </div>
        <div id="md-html" style="z-index: 0">
            <textarea style="display:none;"/>
        </div>
    </div>
</template>

<script>
    import $script from 'scriptjs';

    export default {
        name: 'EditorMD',
        components: {},
        props: {
            readOnly: {
                type: Boolean,
                default: true
            },
            preSetContent: {
                type: String,
            },
            editorPath: {
                type: String,
                default: '/editormd/',
            },
            darkWhenReadOnly: {
                type: Boolean,
                default: false
            },
            darkWhenEdit: {
                type: Boolean,
                default: false
            }
        },
        data: () => ({
            instance: null,
            htmlInstance: null

        }),
        watch: {
            darkWhenReadOnly() {
                this.updateHtmlTheme()
            },
            preSetContent(val) {
                //文章详情不会keep-alive，但是编辑页面会，所以得动态设置markdown
                //又因为是异步初始化，所以要判断一下，为空的话则会在初始化后设置内容
                if (this.instance != null) {
                    // console.log("编辑", val.length)
                    this.instance.setMarkdown(val)
                }
                if (this.htmlInstance != null) {
                    // console.log("查看", val.length)
                    this.initEditor()
                }
            },
            darkWhenEdit(val) {
                if (this.instance != null) {
                    if (val) {
                        this.instance.setTheme("dark")
                        this.instance.setEditorTheme("pastel-on-dark")
                        this.instance.setPreviewTheme("dark")
                    } else {
                        this.instance.setTheme("")
                        this.instance.setEditorTheme("default")
                        this.instance.setPreviewTheme("")
                    }
                }
            }
        },
        created() {
        },
        //相当于created中的nextTick，此时dom元素已经渲染完成
        mounted() {
            // async loading js dependencies
            // editormd depdend on jquery and zepto
            $script([
                    `${this.editorPath}js/jquery.min.js`,
                    `${this.editorPath}js/zepto.min.js`,
                    `${this.editorPath}lib/marked.min.js`,
                    `${this.editorPath}lib/prettify.min.js`,

                    `${this.editorPath}lib/raphael.min.js`,
                    `${this.editorPath}lib/underscore.min.js`,
                    `${this.editorPath}lib/flowchart.min.js`,
                ], () => {
                    // console.log("加载js完成1")
                    $script([
                            `${this.editorPath}lib/sequence-diagram.min.js`,
                            `${this.editorPath}lib/jquery.flowchart.min.js`,
                        ], () => {
                            // console.log("加载js完成2")
                            $script(`${this.editorPath}/editormd.min.js`, () => {
                                    // console.log("initEditor before")
                                    this.initEditor();
                                    // console.log("initEditor after")
                                }
                            )
                        }
                    )
                }
            );
            this.updateHtmlTheme()
        },
        beforeDestroy() {
        },
        methods: {
            updateHtmlTheme() {
                let mdObject = document.getElementById("md-html")
                // console.log(this.darkWhenReadOnly)
                if (this.darkWhenReadOnly) {
                    mdObject.setAttribute("class", "markdown-body-dark editormd-html-preview")
                } else {
                    mdObject.setAttribute("class", "markdown-body editormd-html-preview")
                }
            },
            initEditor() {
                let _this = this
                let theme = ""
                let editorTheme = "default"
                if (this.darkWhenEdit) {
                    theme = "dark"
                    editorTheme = "pastel-on-dark"
                }
                // console.log("theme", theme)
                // eslint-disable-next-line
                this.$nextTick((editorMD = window.editormd) => {
                    if (editorMD) {
                        // Vue 异步执行 DOM 更新，template 里面的 script 标签异步创建
                        // 所以，只能在 nextTick 里面初始化 editor.md
                        if (this.readOnly) {
                            this.htmlInstance = editorMD.markdownToHTML("md-html", {
                                    markdown: _this.preSetContent,
                                    htmlDecode: "style,script,iframe",  // you can filter tags decode
                                    emoji: true,
                                    // toc: false,
                                    // tocStartLevel : 2      // Parse beginning of H2, Default value 1
                                    // tocContainer: "#tocContainer", // 自定义 ToC 容器层
                                    // tocDropdown: true,
                                    tocTitle: "目录",
                                    // tocm: false,
                                    taskList: true,
                                    tex: true,  // 默认不解析
                                    flowChart: true,  // 默认不解析
                                    sequenceDiagram: true  // 默认不解析
                                },
                            )
                        } else {
                            this.instance = editorMD('editor-md', {
                                width: "100%",
                                height: 700,
                                //自动高度
                                // autoHeight: true,
                                path: "/editormd/lib/",
                                placeholder: "写点什么吧...",
                                // tocm: true, // Using [TOCM]
                                tocContainer: "#tocContainer", // 自定义 ToC 容器层
                                tocDropdown: true,
                                tocTitle: "目录",
                                // syncScrolling: false,
                                //工具栏
                                // toolbar: false,
                                //只读模式
                                // readOnly: false,
                                // saveHTMLToTextarea: false,
                                //实时预览
                                // watch: false,
                                styleActiveLine: false,       // disable active line
                                // lineNumbers: false,       // hide line numbers
                                theme: theme,
                                previewTheme: theme,
                                editorTheme: editorTheme,
                                codeFold: true,
                                // saveHTMLToTextarea: true,    // 保存 HTML 到 Textarea
                                searchReplace: true,
                                // htmlDecode: "style,script,iframe|on*",            // 开启 HTML 标签解析，为了安全性，默认不开启
                                //previewCodeHighlight : false, // 关闭预览 HTML 的代码块高亮，默认开启
                                emoji: true,
                                taskList: true,
                                tex: true,                   // 开启科学公式TeX语言支持，默认关闭
                                flowChart: true,             // 开启流程图支持，默认关闭
                                sequenceDiagram: true,       // 开启时序/序列图支持，默认关闭,
                                //dialogLockScreen : false,   // 设置弹出层对话框不锁屏，全局通用，默认为true
                                //dialogShowMask : false,     // 设置弹出层对话框显示透明遮罩层，全局通用，默认为true
                                //dialogDraggable : false,    // 设置弹出层对话框不可拖动，全局通用，默认为true
                                //dialogMaskOpacity : 0.4,    // 设置透明遮罩层的透明度，全局通用，默认值为0.1
                                //dialogMaskBgColor : "#000", // 设置透明遮罩层的背景颜色，全局通用，默认为#fff
                                imageUpload: true,
                                imageFormats: ["jpg", "jpeg", "gif", "png", "bmp", "webp"],
                                imageUploadURL: this.API.URL.UPLOAD_ONE_FILE,
                                onload: function () {
                                    // console.log('editor-md', this);
                                    //this.fullscreen();
                                    //this.unwatch();
                                    //this.watch().fullscreen();

                                    this.setMarkdown(_this.preSetContent);
                                    //this.width("100%");
                                    //this.height(480);
                                    //this.resize("100%", 640);
                                },
                                onchange: function () {
                                    _this.$emit("update:transferContent", this.getMarkdown())
                                }
                            })
                        }
                    }
                });
            },
        },
    }
    ;
</script>

<style scoped>
    .toc-container {
        border: 1px solid #ddd;
        width: 90%;
        margin: 0 auto 15px;
        overflow: visible;
    }
</style>